const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			types: '',
			collectionList: {},
			money: '',
			imgNew: '',
			imgShow: '',
			imgAlipay: '',
			imgAlipayShow: '',
			imgList: ''
		}
	},
	onLoad() {
		this.getList()
	},
	methods: {
		radioChange(e) {
			console.log(e);
			this.types = e.detail.value;
			console.log(this.types)
		},
		getList() {
			uni.request({
				url: `${helper.requestUrl}/promote-rebates`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					console.log(res, '++++');
					if (res.data.status_code == 200) {
						this.collectionList = res.data;
					} else {

					}
				}
			});
		},
		inputValue(e){
			console.log(e,'999')
			this.money = e.detail.value
		},
		getCard(e){
			console.log(e)
			let type = e
			this.getImg(type)
		},
		//上传图片
		getImg(op){
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'],
				success: res => {
					uni.showToast({
						title: '图片上传中',
						icon: 'loading'
					});
					Promise.all(
						res.tempFiles.map(item => {
							return new Promise((resolve, reject) => {
								uni.uploadFile({
									url: `${helper.requestUrl}/posts/uploads`,
									filePath: item.path,
									name: 'file',
									header: {
										authorization: app.globalData.token
									},
									success: res => {
										console.log(res)
										resolve({
											path: JSON.parse(res.data).data
										});
									}
								});
							});
						})
					).then(e => {
						uni.hideToast();
						console.log(e[0].path.path,'++++++++++')
						if(op == '1'){
							this.imgNew = e[0].path.nositepath
							this.imgShow =e[0].path.path
						}else if(op == '2'){
							this.imgAlipay = e[0].path.nositepath
							this.imgAlipayShow =e[0].path.path
						}
						
					}).catch(err => console.log(err));
				}
			});
		},
		cashOut() {
			if(this.types == '0'){
				this.imgList = this.imgNew
			}else if(this.types == '1'){
				this.imgList = this.imgAlipay
			}
			if (this.money == '') {
				uni.showToast({
					title: '请输入提现金额',
					icon: 'none'
				})
				return false
			}
			if (this.money > this.collectionList.user_blance && this.money > '0') {
				uni.showToast({
					title: '提现金额不能大于余额且低于0',
					icon: 'none'
				})
				return false
			}
			if (this.types == '') {
				uni.showToast({
					title: '请选择提现方式',
					icon: 'none'
				})
				return false
			}
			if (this.imgList == '') {
				uni.showToast({
					title: '请上传收款码',
					icon: 'none'
				})
				return false
			}
			uni.request({
				url: `${helper.requestUrl}/user/cash-withdrawals-apply`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data:{
					money: this.money,
					types: this.types,
					urlimages: this.imgList
				},
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.statusCode == 200) {
						uni.showToast({
							title: res.data.msg,
							icon: 'success'
						})
						// this.getList();
						setTimeout( e =>{
							uni.navigateBack({
								delta: 1
							})
						},2000)
					} else {
						uni.showToast({
							title: res.data.msg
						});
					}
				}
			});
		},
		
	}
}
